<template>
  <div class="loader">
    <div class="header">
      <header>
        <a class="site-logo" href="http://www.meituan.com"></a>
      </header>
    </div>
    <div class="main">
      <div class="promotion-banner">
        <img
          src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg"
          width="480"
          height="370"
          alt=""
        />
      </div>
      <div class="form">
        <div class="validate-info">
          <p v-if="error" class="error">
            <i class="iconjinzhi"></i>
            {{ error }}
          </p>
        </div>
        <p class="title">
          <span></span>
          <a href="#">
            
            <i class="iconshouji"></i>
          </a>
        </p>
        <el-input
          v-model="userName"
          placeholder=""
          prefix-icon="iconyonghu"
        ></el-input>
        <el-input
          v-model="passWord"
          type="password"
          placeholder=""
          prefix-icon="iconmima"
        ></el-input>
        <p class="notpass fix">
          <a href="#"></a>
        </p>
        <el-button type="success" @click="submit"> </el-button>
        <p class="signup">
          <span></span>
          <router-link :to="{ name: 'register' }"></router-link>
        </p>
      </div>
      <div class="wrapper-footer">
        <h3></h3>
        <p></p>
      </div>
    </div>
    <div class="footer">
      <footer>
        <div class="site-info-nav">
          <ul class="fix">
            <li>
              <a href="#"></a>
            </li>
            <li>
              <a href="#"></a>
            </li>
            <li>
              <a href="#"></a>
            </li>
            <li>
              <a href="#"></a>
            </li>
            <li>
              <a href="#"></a>
            </li>
          </ul>
        </div>
        <div class="copyright">
          <span
            >2020  meituan.com ICP070791
            11010502025545</span
          >
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import api from "@/api";
export default {
  data() {
    return {
      userName: "",
      passWord: "",
      error: ""
    };
  },
  methods: {
    submit() {
      if (!this.userName) {
        this.error = "";
        return false;
      }
      if (!this.passWord) {
        this.error = "";
        return false;
      }
      api
        .getLogin({
          params: {
            userName: this.userName,
            password: this.passWord
          }
        })
        .then(rem => {
          console.log(rem);
          if (rem.data.status === "success") {
            this.$store.commit("setUserName", this.userName);
            this.$router.push({ name: "index" });
            this.$message("");
          } else {
            this.error = rem.data.msg;
          }
        });
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/page/login.scss";
</style>
<style lang="css">
@import "//at.alicdn.com/t/font_1649205_xylir47por.css";
</style>
